* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-flag-name {
  display: flex;
}

.country-info p {
  margin-top: 0;
  margin-bottom: 8px;
}

.country-flag-name img {
  display: block;
  object-fit: cover;
  height: auto;
  margin-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
